import styled from "styled-components";
import { useEffect } from "react";
import gsap from "gsap";

const arrayOfChars = [
    "Hello!",
    "I'm Divya Tripathi",
    "Frontend Developer"
].map((line) => line.split(''));

const FlexTextContainer = styled.div`
    display:flex;
    flex-direction:column;
    flex-wrap:wrap;
    row-gap:2px;
    justify-content:flex-end;
    
`;
const TitleChars = styled.div`
color:#F5F5F5;
font-family:"mouse-memoirs";
font-weight: 400;
font-size: 5rem;
line-height: 8.7rem;
position: relative;

`

const TitleCharsContainer = styled.div`
    max-width:100%;
    heigth:100vh;
    display:flex;
    flex-direction:row;
    `

export default function TextContainer() {

    useEffect(() => {
        gsap.to(".titleChars", {
            duration: 1,
            scale: 1,
            x: -40,
            yoyo: true,
            repeat: 1,
            color: "red",
            ease: "power1.inOut",
            stagger: {
                amount: 0.8,

            }
        })
    })



    return (
        <FlexTextContainer className="flexTextContainer">
            {arrayOfChars.map((item, i) => {
                return (
                    <TitleCharsContainer key={i}>
                        {item.map((char, i) => {
                            return (
                                <TitleChars key={i} className="titleChars">
                                    {char === " " ? <>&nbsp;</> : char}
                                </TitleChars>
                            )
                        })}

                    </TitleCharsContainer>
                )
            })}

        </FlexTextContainer>
    )
}
