import styled from "styled-components";
import { useEffect, useState, useRef } from "react";
import gsap from "gsap";

import TextContainer from "../TextContainer";
import NavBar from "../NavBar";
import image from "./../img.gif";
import stillImage from "./../stillPicture.png";


const MainContainer = styled.div`
display:flex;
flex-wrap:nowrap;
flex-direction:column;
justify-content:center;
gap:100px;
box-sizing: border-box;
`
const ImgBox = styled.div`
cursor:pointer;
border-radius:50%;
background-image: url(${image});
width:500px;
height:500px;
background-size: cover;
background-position: center;
`
const ImageTextContainer = styled.div`
display:flex;
flex-direction:row;
justify-content:center;
align-items:center;
flex:1;
margin-top:240px
`
export default function MainWrapper() {
    const [isShown, setIsShown] = useState(false);
    const [showImage, setshowImage] = useState(false);
    const imgRef = useRef<(HTMLImageElement)>(null);

    useEffect(() => {
        setTimeout(() => {
            gsap.to(".imgBox", {
                left: 30,
                top: 18,
                position: "absolute",
                duration: 1,
                delay: 1,
                ease: "sine",
                width: 230,
                height: 230,
                stagger: {
                    amount: 2.5,
                },

            })

            setIsShown(!isShown);
            setshowImage(!showImage);
        }, 5000)

    }, []);

    useEffect(() => {
        if (imgRef && imgRef.current && showImage) {
            setTimeout(() => {
                if (imgRef && imgRef.current) {
                    imgRef.current.style.backgroundImage = `url(${stillImage})`;
                    gsap.to(".imgBox", {
                        ease: "slow",
                        yoyo: true,
                        duration: 1,
                        rotate: "+=360",
                        position: "absolute",
                        width: 230,
                        height: 230,
                        left: 30,
                        top: 18,
                    })
                }
            }, 4000)
        }


    }, [showImage, imgRef]);


    return (
        <MainContainer id="mainContainer">
            {isShown && <NavBar />}
            <ImageTextContainer className="imageTextContainer">
                <ImgBox className="imgBox" ref={imgRef}>
                </ImgBox>
                <TextContainer />
            </ImageTextContainer>


        </MainContainer>

    )


}