import gsap from "gsap";
import { useEffect } from "react";
import styled from "styled-components";
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGithub, faLinkedin } from "@fortawesome/free-brands-svg-icons";
library.add(faGithub, faLinkedin);


const HeaderContainer = styled.div`
background-color:#eb4960;
border:2px solid #EF8292;
display:flex;
flex-direction:row;
justify-content: flex-end;
align-items:center;
height:120px;
gap:30px;
position: absolute;
width: 100%;
top: 0;

`

const IconContainer = styled.div`
display:flex;
gap:12px;
visibility: hidden;
opacity:0.1;
`
const StyledFontAwesomeIcon = styled(FontAwesomeIcon)`
cursor: pointer;
font-size:35px;
`
const Link = styled.a`
cursor:pointer;
:visited {
    color:black
}
`

export default function NavBar() {
    useEffect(() => {
        setTimeout(() => {
            gsap.to(".iconContainer", {
                duration: 1,
                ease: "power4.inOut",
                x: -20,
                yoyo: true,
                visibility: "visible",
                opacity: 1,
                delay: 1,
                stagger: 0.08,
            })
        }, 0)
    });

    return (
        <HeaderContainer className="headerContainer">
            <IconContainer className="iconContainer">
                <Link href="https://github.com/t-divya">
                    <StyledFontAwesomeIcon className="fontAwesomeIcon" icon={faGithub} />
                </Link>
                <Link href="https://www.linkedin.com/in/t-divya/">
                    <StyledFontAwesomeIcon className="fontAwesomeIcon" icon={faLinkedin} />
                </Link>
            </IconContainer>
        </HeaderContainer>
    )
}